export default {
  toString: {
    usage:'toString()  Number ',
    grammar: 'num.toString([radix])',
    paramTypes:['Number'],
    paramUsages: [
      '(236) radix  10',
    ]
  }, 
  toFixed: {
    usage:'toFixed() ',
    grammar: 'num.toFixed(digits)',
    paramTypes:['Number'],
    paramUsages: [
      ' 0  20  0',
    ]
  }, 
  toExponential: {
    usage:'toExponential() ',
    grammar: 'num.toExponential(fractionDigits)',
    paramTypes:['Number'],
    paramUsages: [
      '',
    ]
  },
  toPrecision: {
    usage:'toPrecision() ',
    grammar: 'num.toPrecision(precision)',
    paramTypes:['Number'],
    paramUsages: [
      '',
    ]
  },
}



